@import "../Style/colors";

.about-franchise {
  &__banner {
    background-image: url('../images/About - Francorp.jpg');
    background-position: center;
    background-color: $secondaryBlueColor;
    color: $whiteColor;
    font-size: 36px;
    font-weight: 600;
    padding: 120px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
  }
  &__body {
    padding: 60px;
    text-align: justify;
    &-title {
      padding-bottom: 15px;
      color: $primaryBlueColor;
      font-size: 20px;
      font-weight: 500;
    }
    &-text {
      color: $greyColor;
      font-size: 14px;
      padding-bottom: 20px;
    }
    &-banner {
      text-align: center;
      img {
        width: 30%;
      }
    }
  }
}

.about-franchise__body.change-order{
  text-align: right;
  direction: rtl;
}
