@import '../Style/colors';

.popup-container{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;

    background-color: $secondaryBlueColorLight1;
    display: flex;
    justify-content: center;
    align-items: center;
    &-inner{
        position: relative;
        width: 100%;
        height: 450px;
        max-width: 800px;
        background-color: $whiteColor;
        &-close{
            position: absolute;
            top: 16px;
            right: 16px;
            font-size: 30px;
            background-color: $whiteColor;
            border: none;            
        }
    }
}
