@import '../Style/colors';

.about-partnership{
    &__banner{
        background-image: url("../images/Franchise Development Program.jpg");
        background-position: center;
        background-size: cover;
        background-color: $secondaryBlueColor;
        color: $whiteColor;
        font-size: 36px;
        font-weight: 600;
        padding: 120px;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    &__body{
        padding: 60px;
        text-align: justify;
        color: $greyColor;
    }
}