@import '../Style/colors';

.about-khalifafund{
    &__banner{
        background-image: url('../images/khalifa_fund_banner.jpg');
        background-position: center;
        background-color: $secondaryBlueColor;
        color: $whiteColor;
        font-size: 36px;
        font-weight: 600;
        padding: 120px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-repeat: no-repeat;
        background-size: cover;
    }
    &__body{
        padding: 60px;
        text-align: justify;
        &-banner{
            text-align: center;
            direction: rtl;
            img{
                width: 30%;
            }

        }
    }
    &__body p, li{
        color: $greyColor;
    }
    &__body h3, h4{
        color: $primaryBlueColor;
    }
}
.about-khalifafund__body.change-order{
    *{
        text-align: initial;
        direction: rtl !important;

    }
    ul{
        direction: rtl;
    }
}
.about-khalifafund__body-banner{
    direction: unset !important;
    display: flex;
    justify-content: center;
    direction: rtl !important;
}