@import '../Style/colors';

.franchise-sales{
    min-height: 100vh;
    &__banner{
        background-image: url("../images/Franchise Sales Training.jpg");
        background-position: center;
        background-color: $secondaryBlueColor;
        color: $whiteColor;
        font-size: 36px;
        font-weight: 600;
        padding: 120px;
        display: flex;
        justify-content: center;
    background-size: cover;
    align-items: center;
  
      @media (max-width:425px) {
        padding: 70px;
      }
    }
    &__body{
        padding: 80px;
        @media (max-width:425px) {
            padding: 40px;
          }
        &-title{
            text-align: center;
            color: $primaryBlueColor;
            padding-bottom: 20px;
            font-size: 20px;
            font-weight: 600;
        }
        &-text{
            text-align: justify;
            color: $greyColor;
        }
        &-join{
            padding-top: 30px;
            &-button{
                color: $whiteColor;
                background-color: $primaryBlueColor;
                padding: 10px 20px;
                border: none;
            }

        }
    }
    &__popup{
        &-title{
            color: $primaryBlueColor;
            font-size: 20px;
            text-align: center;
            font-weight: 500;
            padding-bottom: 20px;
        }
        &-form{
            padding: 100px 30px;
        }
        &-row {
            width: 100%;
            display: flex;
            justify-content: space-around;

            &-input {
                width: 45%;
                background: transparent;
                padding: 10px 5px;
                border: none;
                border-bottom: 1px solid $greyColor;
                margin: 10px 0;

                &:focus {
                    outline: none;
                }
            }
            &-message {
                width: 97%;
                background: transparent;
                padding: 10px 5px;
                border: none;
                border-bottom: 1px solid $greyColor;
                margin: 10px 0;

                &:focus {
                    outline: none;
                }
            }
        }

        &-action {
            display: flex;
            justify-content: center;
            margin: auto;
            background-color: $secondaryBlueColor;
            color: $whiteColor;
            width: 150px;
            padding: 8px 15px;
            font-weight: 500;
            margin: 10px 0;
            cursor: pointer;
            border: none;

            &:focus {
                outline: none;
            }

            &-disabled {
                cursor: not-allowed;
                background-color: $secondaryBlueColorLight1;
            }
        }
    }
}
.franchise-sales.change-order{
    *{
        text-align: initial;
        direction: rtl;
    }
    .franchise-manual__body-text{
        direction: rtl;
      }
      .franchise-manual__body-title{
        text-align: center !important;
      }
}
.franchise-sales__popup-title{
    text-align: center !important;
}