body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
 
}
.font-arbic{
  font-family: "Bahij Fedra Arabic" !important ;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
@font-face {
  font-family: "Bahij Fedra Arabic";
  src: url("./assets/Fonts/BahijFedraArabic.eot") format("embedded-opentype"),
    url("./assets/Fonts/BahijFedraArabic.woff2") format("woff2"),
    url("./assets/Fonts/BahijFedraArabic.woff") format("woff"),
    url("./assets/Fonts/BahijFedraArabic.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Bahij Greta Arabic";
  src: url("./assets/Fonts/BahijGretaArabic-Bold.eot")
      format("embedded-opentype"),
    url("./assets/Fonts/BahijGretaArabic-Bold.woff2") format("woff2"),
    url("./assets/Fonts/BahijGretaArabic-Bold.woff") format("woff"),
    url("./assets/Fonts/BahijGretaArabic-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Bahij Fedra Arabic";
  src: url("./assets/Fonts/BahijFedraArabic-Bold.eot")
      format("embedded-opentype"),
    url("./assets/Fonts/BahijFedraArabic-Bold.woff2") format("woff2"),
    url("./assets/Fonts/BahijFedraArabic-Bold.woff") format("woff"),
    url("./assets/Fonts/BahijFedraArabic-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
}

input[type=number] {
  -moz-appearance: textfield;
}
.direction-rtl{
  direction: rtl;
}